import React from 'react';
import classes from './Footer.module.css';

const Footer = () => {
    return (
        <div className={classes.Footer}>
            <div className={classes.FooterWrapper}>
                <div className={classes.SocialWrap}>
                    <h3>ΚΑΝΕΛΟΡΙΖΑ</h3>
                    <div className={classes.Shop}>   
                        <ul className={classes.Social}>
                            <li><a href="https://www.facebook.com/%CE%9A%CE%B1%CE%BD%CE%B5%CE%BB%CF%8C%CF%81%CE%B9%CE%B6%CE%B1-106641364873102"><img src="./assets/icon/facebook.svg" alt="facebook logo"/></a></li>
                            <li><a href="https://www.instagram.com/kaneloriza.ptolemaida/"><img src="./assets/icon/instagram.svg" alt="instagram logo"/></a></li>
                        </ul>
                    </div>
                
                    <div className={classes.Address}> 
                        <a href="https://www.google.com/maps/@40.5149825,21.6870977,18z">
                            <address>Αργυρουπόλεως 47<span>Πτολεμαΐδα</span></address>  
                        </a>  
                    </div>
                </div>
                <div className={classes.Developer}>
                    <p>Web App Developed And Designed by</p>
                    <h6><a href="https://www.instagram.com/yanniotis/">Petros Gianniotis</a></h6>
                </div>    
            </div>          
        </div>
    )
}

export default Footer
