import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import ProductList from '../ProductList/ProductList';
import classes from './Category.module.css';

class Category extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }
    
    render(){
        const index = this.props.match.params.id;
        const cat = this.props.categories[index];
        const products = this.props.getProducts(index);
        
    
        
        return (
            <div className={classes.Category}>
                <h2 >{cat.name[0]}</h2> 
                {cat.hasOwnProperty('header') 
                    ?   <div className={classes.CategoryHeader}>
                            {cat.header}
                        </div> 
                    :   null}

                <ProductList
                    products={products} /> 

                {cat.hasOwnProperty('preFooter') 
                    ?   <div className={classes.CategoryPreFooter}>
                            {cat.preFooter}
                        </div> 
                    :   null}
                {cat.hasOwnProperty('footer') 
                    ?   <div className={classes.CategoryFooter}>
                            {cat.footer}
                        </div> 
                    :   null}
                

                <aside>
                    <p>Στις τιμές περιλαμβάνεται ΦΠΑ</p>
                    <p>Ο καταναλωτής δεν έχει υποχρέωση να πληρώσει εάν δεν
λάβει το Νόμιμο παραστατικό στοιχείο
(Απόδειξη ή Τιμολόγιο)</p>
                </aside>
                    
                <div className={classes.Back} >
                    <Link to='/Menu'>
                        Επιστροφή στο κεντρικό μενού
                    </Link>
                </div>  
            </div>
        )
   }
}

export default Category;
